import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch , useSelector} from 'react-redux';
import axios from "axios";
import "../../style/pages/register.css"

import { getUser } from '../../redux/actions/user';
export default function Register() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [phone, setPhone] = useState('');
    const [errMsg, setErrMsg] = useState('');
    const history = useHistory();
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getUser());
    } , []);
    const reducer = useSelector(state => state.userReducer);
    if (reducer.loading) return(<div>...Lodaing</div>);
    if (reducer.isLogin) history.push('/account');
    const handleSubmit = async (e) => {
        e.preventDefault();
        const url = `http://localhost:5000/register`;
        const data = {
            name,
            email,
            password,
            phone
        }
        axios.post(url, data)
            .then(res => {
                if (res.data.errMsg) setErrMsg(res.data.errMsg)
                else history.push('/login')
            })
            .catch(err => console.log(err))
    }
    return (
        <section className="register-section">
            <form onSubmit={handleSubmit}>
                {errMsg ? <p style={{ color: '#e74c3c' }}>{errMsg}</p> : ''}
                <label htmlFor="">
                    <span>Name</span>
                    <input type="text" onChange={(e) => setName(e.target.value)} />
                </label>

                <label htmlFor="">
                    <span>Email</span>
                    <input type="text" onChange={(e) => setEmail(e.target.value)} />
                </label>

                <label htmlFor="">
                    <span>Password</span>
                    <input type="password" onChange={(e) => setPassword(e.target.value)} />
                </label>

                <label htmlFor="">
                    <span>Phone</span>
                    <input type="number" onChange={(e) => setPhone(e.target.value)} />
                </label>
                <div><button type="submit">Register</button> <a href="/login">Login</a></div>
            </form>
        </section>
    )
}
