import { types } from "../actions/types"
const initialState = {
    products: [],
    loading: false,
}
export const productsReducer = (state = initialState, { type, payload }) => {
    switch (type) {
        case types.PRODUCTS_LOADING:
            return {
                ...state,
                loading: true,
            }
        case types.PRODUCTS_LODAED:
            return {
                ...state,
                products: payload,
                loading: false,
            }

        case types.PRODUCT_LOADING:
            return {
                ...state,
                loading: true,
            };
            
        case types.PRODUCT_LOADED:
            return {
                ...state,
                products: payload.product,
                loading: false,
            };

        default:
            return state
    }
}