import { types } from "./types";
import axios from "axios";
export const getProduct = (id) => dispatch => {
    dispatch({type:types.PRODUCT_LOADING});
    const url = `${process.env.REACT_APP_SERVER_URL}/product/${id}`;
    axios(url)
    .then(res => {
        dispatch({type:types.PRODUCT_LOADED , payload:{product:res.data.product , relatedProducts:res.data.relatedProducts}});
    })
    .catch(err => console.log(err))
}