import {BrowserRouter as Router , Route , Switch} from 'react-router-dom'
import './style/main/app.css'
//COMPONENTS
import Products from './components/pages/products';
import Home from './components/pages/home';
import Login from './components/account/login';
import Register from './components/account/register';
import Header from './components/main/header';
import Account from './components/account/account';
import AddProduct from './components/admin/add_product';
import EditProduct from './components/admin/edit_product';
import Product from './components/pages/product';
import AddCategory from './components/admin/add_category';
import Cart from './components/account/cart';
function App() {
  return (
    <div className="App">
      <Router>
          <Header/>
          <Route exact path="/" component={Home}/>
          <Route exact path="/products" component={Products}/>
          <Route exact path="/login" component={Login}/>
          <Route exact path="/register" component={Register}/>
          <Route exact path="/account" component={Account}/>
          <Route exact path="/cart" component={Cart}/>
          <Route exact path="/add/product" component={AddProduct}/>
          <Route exact path="/product/:path" component={Product}/>
          <Route exact path="/edit/product/:path" component={EditProduct}/>
          <Route exact path="/add/category" component={AddCategory}/>
      </Router>
    </div>
  );
}

export default App;
