import { types } from "../actions/types"
const initialState = {
    product: {},
    loading: false,
    relatedProducts:[]
}

export const productReducer = (state = initialState, { type, payload }) => {
    switch (type) {
        case types.PRODUCT_LOADING:
            return {
                ...state,
                loading: true
            }
        case types.PRODUCT_LOADED:
            return {
                ...state,
                product:payload.product,
                relatedProducts:payload.relatedProducts,
                loading: false
            }
        default:
            return state
    }
}